.about-me {
  background-image: radial-gradient( 
    ellipse, 
    #81B7C1, 
    #36424A
  );
}

.about-me__avatar-header {
  color:#bcc9cc;
  margin-bottom: 1rem;
  font-size: 4rem;
  padding: 1rem 2rem;
}

.about-me__avatar {
  border-radius: 50%;
  width: 100%;
  border: 0.2rem solid #B8D1C5;
}

.about-me__avatar-wrapper {
  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
  border: 0.5rem solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    border: 0.5rem solid rgba(255, 255, 255, 0.1);
    top: -1.5rem;
    bottom: -1.5rem;
    right: -1.5rem;
    left: -1.5rem;
    border-radius: 50%;
  }
}